.cart-dropdown-container {
  position: absolute;
  width: 240px;
  height: 340px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid black;
  background-color: white;
  top: 90px;
  right: 40px;
  z-index: 5;

  .empty-message {
    font-size: 18px;
    margin: 10px auto;
  }

  .cart-items {
    height: 240px;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
  }

  ::-webkit-scrollbar {
    background: white;
    width: 7px;
  }

  ::-webkit-scrollbar-thumb {
    background: lightgray;
    border-radius: 5px;
  }

  button {
    margin-top: auto;
    font-size: 13px;
  }
}
